import React, { useState } from 'react'

export default function ItemDetail({nombre,familia,precio,texto,img,stock,botonera,detalle}){
return (

<section className="mb-5">

  <div className="row m-5">
    <div className="col-md-6 mb-4 mb-md-0">

      <div id="mdb-lightbox-ui"></div>

      <div className="mdb-lightbox">

        <div className="row product-gallery mx-1">

          <div className="col-12 mb-0">
            <figure className="view overlay rounded z-depth-1" >
              <a href={process.env.PUBLIC_URL + img}>
                <img src={process.env.PUBLIC_URL + img} alt={nombre}
                  className="img-fluid z-depth-1"/>
              </a>
            </figure>
          </div>
        </div>

      </div>

    </div>
    <div className="col-md-6">

      <h5>{nombre}</h5>
      <p className="mb-2 text-muted text-uppercase small">{familia}</p>
      <p><span className="mr-1"><strong>{precio}</strong></span></p>
      

      <p className="pt-1">DESCRIPCION</p>


      <div className="table-responsive mb-2">
        <table className="table table-sm table-borderless">
          <tbody>
            <tr>
              <td className="pl-0 pb-0 w-25">CANTIDAD</td>
            </tr>
            <tr>
              <td className="pl-0">
                <div className="def-number-input number-input safari_only mb-0">
                  <button onclick="this.parentNode.querySelector('input[type=number]').stepDown()"
                    className="minus"></button>
                  <input className="quantity" min="0" name="quantity" value="1" type="number"/>
                  <button onclick="this.parentNode.querySelector('input[type=number]').stepUp()"
                    className="plus"></button>
                </div>
              </td>

            </tr>
          </tbody>
        </table>
      </div>
      <button type="button" className="btn btn-primary btn-md mr-1 mb-2">Comprar</button>
      <button type="button" className="btn btn-light btn-md mr-1 mb-2"><i
          className="fas fa-shopping-cart pr-2"></i>Agregar al carrito</button>
    </div>
  </div>

</section>
)
}